<template>
    <div class="system flex" ref="mianscroll">
        <div class="left"  :class="[isMobile?'mobileLeft':'']">
            <zz-tree ref="tree" :datalist="treeData" :nodeClick.sync="currentTree" width="200"></zz-tree>
        </div>
        <div class="right" :class="[isMobile?'mobileRight':'']">
            <div class="con" >
                <component class="markdown-body" :is="classnames"></component>
            </div>
        </div>
    </div>
</template>
