                {fjid: 1,sid: 3,label: "平面类2",classnames: "pml2"},
                {fjid: 1,sid: 4,label: "平面类3",classnames: "pml3"},
                {fjid: 1,sid: 5,label: "立体类",classnames: "ltl"},
                {fjid: 1,sid: 6,label: "类比推理",classnames: "lbtl"},
                {fjid: 1,sid: 7,label: "定义判断",classnames: "dypd"},
                {fjid: 1,sid: 8,label: "翻译推理",classnames: "fytl"},
                {fjid: 1,sid: 9,label: "分析推理",classnames: "fxtl"},

                {fjid: 1,sid: 10,label: "真假推理",classnames: "zjtl"},
                {fjid: 1,sid: 11,label: "论证",classnames: "lz"},
            ],
