.left {
    width: 250px;
    background: #fff;
    margin-right: 15px;
    border-right: 1px solid #eaecef;
}
.mobileLeft{
    width: 1.6rem;
    margin-right: 0px;
    height: calc(100vh - 60px);
    overflow-y: auto;
    .zz-tree{
        width: 1.6rem!important;
        /deep/ .el-icon-caret-right{
            display: none!important;
        }
        /deep/.customTree{
            text-indent: 16px;
            font-size: 14px;
        }
    }
}

.right {
    width: 100%;
    height: 100%;
    background: #fff;
    flex: 1;
    padding: 15px;
    box-sizing: border-box;
    overflow-y: auto;
    .con{
